@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

#manage_btn {
	position: absolute;
	right: 3%;
	top: 30px;
	width: 200px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.header {
	background-image: url(https://thumbs.gfycat.com/PolishedFarawayElk-size_restricted.gif);
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	background-position-y: -200px;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 400px;
}

.header h1 {
	text-shadow: 4px 4px 8px black;
	font-family: "Kalam", cursive;
	font-size: 110px;
	color: white;
	text-align: center;
	width: 100%;
	line-height: 0.9;
}

.header p {
	text-shadow: 2px 2px 4px black;
	color: white;
	font-family: "Kalam", cursive;
	font-size: 34px;
	width: 100%;
}

.search {
	height: 60px;
}

.search form .input-group {
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.result {
	background-color: #f2f2f2;
	width: 100%;
}

.item {
	display: inline-block;
	margin: 10px;
	box-sizing: border-box;
	width: 15%;
	border-radius: 5px;
	overflow: hidden;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.09);
}

.item.each_news {
	height: 350px;
	width: 700px;
	position: relative;
}

.item_head.each_news {
	height: 350px;
	overflow: hidden;
	position: absolute;
}

.item_head.each_news img {
	object-fit: cover;
}

.each_news_link {
	color: black;
}

.item.eachArtist:hover,
.each_news_link:hover {
	-webkit-filter: brightness(0.9);
	        filter: brightness(0.9);
}

a.a_item:hover,
.each_news_link:hover {
	color: unset !important;
}

.item_body_outer {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 130px;
	background-color: white;
}

.item_body_outer.each_news {
	position: absolute;
	background-color: rgba(0, 0, 0, 0.7);
	color: white;
	width: 100%;
	bottom: 0;
}

.item_body {
	padding: 10px 0;
	width: 80%;
}

.name.each_news {
	color: white !important;
	font-weight: normal !important;
	font-size: 22px !important;
	font-family: "Roboto";
}

.item .name {
	color: black;
	font-weight: bold;
}

.item .fansNum,
.item .albumNum {
	color: black;
	font-weight: bold;
	margin-bottom: 0;
	text-decoration: none;
}

.item .fansNum span,
.item .albumNum span {
	color: grey;
	font-weight: normal;
}

.spinner-border {
	margin-bottom: -30px;
}

.nav_bar {
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.09);
	position: fixed;
	z-index: 100;
	width: 100%;
	height: 50px;
	background-color: white;
	padding: 0 3%;
}

.nav_bar .back_link {
	color: #d2a3a9;
	text-decoration: none;
	font-size: 24px;
	line-height: 1.8;
	display: inline-block;
	width: 10%;
	box-sizing: border-box;
}

.nav_bar .back_link img {
	margin-top: -5px;
}

.nav_bar .page_title {
	display: inline-block;
	text-align: center;
	width: 80%;
	font-weight: bold;
	font-size: 24px;
	margin-top: 5px;
	box-sizing: border-box;
}

#manage_btn2 {
	height: 40px;
	line-height: 0.6;
	margin-top: -8px;
}

#manage_btn2_outer {
	box-sizing: border-box;
	display: inline-block;
	width: 10%;
}

#eachArtist_body {
	background-color: #f2f2f2;
}

#eachArtist_content {
	padding-top: 70px;
	background-color: #f2f2f2;
	width: 90%;
	display: block;
	margin: 0 auto;
}

#top_track_content,
#related_news_content {
	text-align: center;
}

#top_track,
#related_news {
	border-radius: 10px;
	padding: 20px;
	margin: 20px 0;
	background-color: white;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.09);
}

#top_track #top_track_title,
#related_news #related_news_title {
	margin-bottom: 10px;
	text-align: center;
	font-weight: bold;
}

.each_track {
	display: inline-block;
	margin: 30px 20px 10px 20px;
}

.each_track .track_title {
	font-size: 18px;
	font-weight: bold;
	color: rgba(0, 0, 0, 0.7);
	text-align: center;
}

.each_track .track_recording {
	text-align: center;
}

.each_track .track_link {
	text-align: center;
}

.item_body .name {
	font-size: 20px;
}

.news_source_date {
	width: 100%;
}

.news_source_date .news_source {
	background-color: #0c71e0;
	padding: 0px 10px 2px 10px;
	border-radius: 5px;
	color: rgba(255, 255, 255, 0.9);
	font-size: 16px;
}

.news_source_date .news_date {
	background-color: rgba(255, 255, 255, 0.2);
	padding: 0px 10px 2px 10px;
	border-radius: 5px;
	color: rgba(255, 255, 255, 0.9);
	font-size: 16px;
}

#save_artist_btn {
	width: 220px;
	font-size: 20px;
	font-weight: 600;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.09);
}

.eachArtist.eachSavedArtist {
	width: 290px;
	height: 450px;
}

.item_body_outer.eachSavedArtist {
	height: 160px;
}

.result.savedArtist {
	border-radius: 10px;
}

.mm-popup {
	display: none;
}

.mm-popup--visible {
	display: block;
}

.mm-popup__overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1000;
	overflow: auto;
	background: rgba(0, 0, 0, 0.1);
}

.mm-popup__close {
	position: absolute;
	top: 15px;
	right: 20px;
	padding: 0;
	width: 20px;
	height: 20px;
	cursor: pointer;
	outline: none;
	text-align: center;
	border-radius: 10px;
	border: none;
	text-indent: -9999px;
	background: transparent
		url("data:image/png;base64,")
		no-repeat center center;
	background-size: 100%;
	margin: 0;
}

.mm-popup__input {
	display: block;
	width: 100%;
	height: 30px;
	border-radius: 3px;
	background: #f5f5f5;
	border: 1px solid #e9ebec;
	outline: none;
	box-sizing: border-box !important;
	font-size: 14px;
	padding: 0 12px;
	color: #808080;
}

.mm-popup__btn {
	border-radius: 3px;
	box-sizing: border-box;
	padding: 0 10px;
	margin: 0;
	line-height: 32px;
	height: 32px;
	border: 1px solid #666;
	text-align: center;
	display: inline-block;
	font-size: 12px;
	font-weight: 400;
	color: #333;
	background: transparent;
	outline: none;
	text-decoration: none;
	cursor: pointer;
	font-family: "Open Sans", sans-serif;
}

.mm-popup__btn--success {
	background-color: #27ae60;
	border-color: #27ae60;
	color: #fff;
}

.mm-popup__btn--danger {
	background-color: #c5545c;
	border-color: #c5545c;
	color: #fff;
}

.mm-popup__box {
	width: 350px;
	position: fixed;
	top: 10%;
	left: 50%;
	margin-left: -175px;
	background: #fff;
	box-shadow: 0px 5px 20px 0px rgba(126, 137, 140, 0.2);
	border-radius: 5px;
	border: 1px solid #b8c8cc;
	overflow: hidden;
	z-index: 1001;
}

.mm-popup__box__header {
	padding: 15px 20px;
	background: #edf5f7;
	color: #454b4d;
}

.mm-popup__box__header__title {
	margin: 0;
	font-size: 16px;
	text-align: left;
	font-weight: 600;
}

.mm-popup__box__body {
	padding: 20px;
	line-height: 1.4;
	font-size: 14px;
	color: #454b4d;
	background: #fff;
	position: relative;
	z-index: 2;
}

.mm-popup__box__body p {
	margin: 0 0 5px;
}

.mm-popup__box__footer {
	overflow: hidden;
	padding: 20px 20px 20px;
}

.mm-popup__box__footer__right-space {
	float: right;
}

.mm-popup__box__footer__right-space .mm-popup__btn {
	margin-left: 5px;
	width: 100px;
}

.mm-popup__box__footer__left-space {
	float: left;
}

.mm-popup__box__footer__left-space .mm-popup__btn {
	margin-right: 5px;
}

.mm-popup__box--popover {
	width: 300px;
	margin-left: -150px;
}

.mm-popup__box--popover .mm-popup__close {
	position: absolute;
	top: 5px;
	right: 5px;
	padding: 0;
	width: 20px;
	height: 20px;
	cursor: pointer;
	outline: none;
	text-align: center;
	border-radius: 10px;
	border: none;
	text-indent: -9999px;
	background: transparent
		url("data:image/png;base64,")
		no-repeat center center;
	background-size: 100%;
	margin: 0;
	z-index: 3;
}

.mm-popup__box--popover .mm-popup__box__body {
	padding: 20px;
}

@media (max-width: 420px) {
	.mm-popup__box {
		width: auto;
		left: 10px;
		right: 10px;
		top: 10px;
		margin-left: 0;
	}

	.mm-popup__box__footer__left-space {
		float: none;
	}

	.mm-popup__box__footer__right-space {
		float: none;
	}

	.mm-popup__box__footer {
		padding-top: 30px;
	}

	.mm-popup__box__footer .mm-popup__btn {
		display: block;
		width: 100%;
		text-align: center;
		margin-top: 10px;
	}
}

